import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";
import { useState } from "react";
function App() {
  // const name = "akram";
  // const x = false;

  const [showAddTask, setShowAddTask] = useState(false);
  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: "I WILL BE A MILLIONAIRE TONIGHT",
      day: "May 8th 7:23pm",
      reminder: true,
    },
    {
      id: 2,
      text: "Eat",
      day: "May 8th 7:23pm",
      reminder: true,
    },
    {
      id: 3,
      text: "Exercise",
      day: "May 8th 7:17pm",
      reminder: false,
    },
    {
      id: 4,
      text: "Appointment",
      day: "May 8th 7:15pm",
      reminder: true,
    },
  ]);

  // Add Task
  const addTask = (task) => {
    //console.log(task);
    const id = Math.floor(Math.random() * 1000) + 1;
    const newTask = { id, ...task };
    setTasks([...tasks, newTask]);
  };

  // Delete Task
  const deleteTask = (id) => {
    // console.log("delete", id);
    setTasks(tasks.filter((task) => task.id !== id));
  };

  // Toggle Reminder
  const toggleReminder = (id) => {
    // console.log(id);
    setTasks(
      tasks.map((task) =>
        task.id === id ? { ...task, reminder: !task.reminder } : task
      )
    );
  };

  return (
    <div className="container">
      <Header
        title="Hello YES Hello"
        onAdd={() => setShowAddTask(!showAddTask)}
        showAdd={showAddTask}
      />
      {showAddTask && <AddTask onAdd={addTask} />}
      {/* <h1>Hello From App.js to index.js to rootdiv in index to index.html</h1> */}
      {/* <h2>
        My name is {name} {1 + 1}
      </h2> */}
      <h2>
        {/* x ? 'Yes' if x then show yes : 'No' else show np */}
        {/* hello {x ? "Yes" : "No"} */}
      </h2>
      {tasks.length > 0 ? (
        <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} />
      ) : (
        "No Tasks"
      )}
    </div>
  );
}

export default App;
